.nav-main-container {
    background-color: #00203FFF;
    color: #ADEFD1FF;
    min-height: 20px;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    align-items: center;
    padding: 14px 80px;
    cursor: pointer;
}
.logo {
    font-size: 20px;
    letter-spacing: 1.1px;
    font-weight: 600;
}
.contact-logo {
    display: flex;
    flex-direction: row;
    gap: 12px;
}