.homepage-main-container {
    padding: 80px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 20px;
    justify-content: space-between;
}

.card {
    margin-top: 30px;
    width: 300px;
    height: 400px;
    padding: 30px;
    border: 1px solid rgba(255, 255, 255, .25);
    border-radius: 20px;
    background-color: rgba(255, 255, 255, 0.45);
    box-shadow: 0 0 10px 1px rgba(0, 0, 0, 0.25);
    backdrop-filter: blur(15px);
  }

.top-findings {
    font-size: 22px;
    font-weight: 600;
    padding-bottom: 20px;
}

.react-p {
    font-size: 18px;
    color: inherit;
    cursor: pointer;
}

.react-p:hover {
    color: red;
}

.img-wrapper > img {
    width: auto;
    height: 380px;
    border-radius: 12px;
}