.container1 {
    width: 340px;
    height: 440px;
    padding: 14px;
    border-radius: 14px;
    margin: 18px;
    box-shadow: 0 0 10px 1px rgba(0, 0, 0, 0.25);
    .wrapper1 > img {
        width: 340px;
    }
    .title_1 {
        padding: 10px 0px;
        font-size: 22px;
        font-weight: 500;
    }
    .content_1 {
        font-size: 16px;
        padding-bottom: 10px;
    }
}
.button-wrapper1 {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    gap: 10px;
    .btn {
        width: 400px;
        height: 40px;
        border-radius: 16px;
        border: 2px solid #00203FFF;
        cursor: pointer;
    }
    .outline{
        color: #f1f1f1;
        background-color: #00203FFF;
    }
    .fill:hover,.outline:hover {
        color: #adadad;
    }
    .fill {
        background-color: #ADEFD1FF;
        color: #00203FFF;
    }
}